.page-contact{
    .section-top{
        background: url("../img/hero-contact.jpg") center / cover;
        }


.section-map-inside{
    iframe{
        width: 100%;
padding: 0 2%;

    }
}

.section-contact-form{
padding-top: 0 !important;
.section-contact-form-content{
display: flex;
margin: 0 2%;
@media screen and (max-width:992px){
flex-direction: column;
}
    .contact-info{
        display: flex;
    flex-direction: column;
    width: 40%;
    @media screen and (max-width:992px){
        width: 100%;
    padding-left: 4%;
    margin-bottom: 4%;
    }
    i{
        color: $color_base;
        font-size: 2rem;
        padding-right: 2.6%;
    }
    .contact-info-line{
        display: flex;
        text-align: left;
        padding-bottom: 1%;
    }
}
.contact-form{
    display: flex;
    width: 100%;
    @media screen and (max-width:992px){

    flex-direction: column;

    }

    .left-inputs, .right-inputs{
        display: flex;
        flex-direction: column;
        width: 50%;
        padding-left: 4%;
    @media screen and (max-width:992px){
        width: 100%;


    }
    }
    input, textarea{
        padding: 4%;
        margin-bottom: 6%;
    }
    textarea{
        height: 212px;
        
        
    }
    .btn{
        width: 60%;
        margin-left: 40%;
    }
}

}

}
}
